/* eslint-disable no-plusplus */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import {
  getListVoucherByTypeHandle,
  getListVoucherTypeHandle,
} from 'src/redux/actions/voucher.action';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import HomeVoucherView from './HomeVoucher.view';

const HomeVoucherContainer = () => {
  const [listData, setListData] = useState([]);
  const dispatch = useDispatch();
  const history = useHistory();
  useEffect(() => {
    dispatch(
      getListVoucherTypeHandle((listVoucherType) => {
        setListData(listVoucherType);
        if (listVoucherType && listVoucherType.length > 0) {
          for (let i = 0; i < listVoucherType.length; i++) {
            const voucherType = listVoucherType[i];
            dispatch(
              getListVoucherByTypeHandle(
                1,
                6,
                { voucher_type: voucherType.id },
                (listVoucher) => {
                  setListData((prevListData) => {
                    const temp = [...prevListData];
                    temp[i] = { ...temp[i], data: listVoucher.data };
                    return temp;
                  });
                }
              )
            );
          }
        }
      })
    );
  }, []);

  const listBanner = useSelector(getListBannerSelector);
  return (
    <HomeVoucherView
      onSeeAllClick={(voucherType) => {
        history.push(`/voucher/see-all/${voucherType.id}`, {
          voucherType,
        });
      }}
      listData={listData}
      listBanner={listBanner}
    />
  );
};

export default HomeVoucherContainer;
