import React from 'react';
import { Grid, Container } from '@material-ui/core';

import { TitleCustom, VoucherItem, Breadcrumb } from 'src/components/base';
import { useHistory } from 'react-router-dom';
import './HomeVoucher.scss';
import { ArrowRightGold } from 'src/assets/icons';

const HomeVoucherView = (props) => {
  const history = useHistory();
  return (
    <div className="voucher-container">
      <Grid container direction="column">
        {/* <---E-Voucher-Section---> */}
        <Breadcrumb items={[{ name: 'Phiếu ưu đãi', url: '/voucher' }]} />
        {/* <Grid container lg={12} md={12} justifyContent="center">
        <Slider onClickSlider={() => {}} data={props.listBanner} />
      </Grid> */}
        <Container maxWidth="lg">
          <TitleCustom title="E-Voucher" customStyle="title-header" />
        </Container>
        {props?.listData?.map((data) => (
          <Container key={data.id} maxWidth="lg">
            {!!data?.data?.length && (
              <div className="title-item">
                <Grid container direction="row" justify="space-between">
                  <div style={{ display: 'flex' }}>
                    <div
                      style={{
                        width: '4px',
                        backgroundColor: '#EABFB9',
                        marginRight: '20px',
                      }}
                    />
                    <p className="title">{data.name}</p>
                  </div>
                  {/* <ButtonCustom title="XEM THÊM" customStyle="customButton" /> */}
                  <div
                    onClick={() => {
                      if (typeof props.onSeeAllClick === 'function') {
                        props.onSeeAllClick(data);
                      }
                    }}
                    href=""
                    className="see-all"
                  >
                    <p className="see-all-txt">Xem tất cả</p>
                    <ArrowRightGold />
                  </div>
                </Grid>
              </div>
            )}

            <Grid container spacing={3} direction="row">
              {data?.data?.map((el) => (
                <VoucherItem
                  onItemClick={(item) => {
                    history.push(`/voucher/${item.id}`);
                  }}
                  item={el}
                />
              ))}
            </Grid>
          </Container>
        ))}
      </Grid>
    </div>
  );
};

export default React.memo(HomeVoucherView);
